@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

html,
body {
  font-size: 14px;
  font-weight: 400;
  overflow-x: hidden;
}

/* копия */
body {
  overflow-anchor: none;
  margin: 0;
}

.container {
  width: 100%;
  max-width: 1200px;
  padding-right: 5px;
  padding-left: 5px;
  margin-right: auto;
  margin-left: auto;
  /* text-align: center; */
}

/* скопировал */
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

.header{border-bottom:1px solid rgba(0,0,0,0.1);margin-bottom:20px}.header__news{height:30px;padding:8px 16px;color:#666;text-align:center;background:#f2f2f2;font-size:10px}.header__container{height:55px;display:flex;justify-content:space-between;align-items:center}.logo{display:inline-block;transform:scale(0.8)}.nav__list{display:flex;justify-content:space-between;align-items:center}.nav__item:not(:last-child){margin-right:16px;padding:10px}.nav__link{text-decoration:none;color:#000}@media screen and (max-width: 991px){.nav{display:none}}.user-nav{min-width:100px;max-width:150px;display:flex;justify-content:space-between}.user-nav .user-nav__btn{outline:none;border:none;background-color:transparent}.bar_icon{color:red}.menu-bars{font-size:2rem;background:none;display:none;z-index:11}@media screen and (max-width: 961px){.menu-bars{display:block;z-index:10}}.overlay{height:100vh;width:100vw;position:absolute;top:0;left:0;opacity:0.6;z-index:9;background:#1a1a1a}.nav-menu{width:250px;height:100vh;display:flex;justify-content:center;position:fixed;top:0;left:-100%;transition:850ms;z-index:10}.nav-menu.active{left:0;transition:350ms}.nav-text{display:flex;justify-content:flex-start;align-items:center;padding:8px 0px 8px 16px;list-style:none;height:60px}.nav-text a{text-decoration:none;color:#000;font-size:18px;width:95%;height:100%;display:flex;align-items:center;padding:0 16px;border-radius:4px}.nav-text a:hover{background-color:#1a83ff}.nav-menu-items{width:100%;background-color:#fff}.navbar-toggle{background-color:#fff;width:100%;height:80px;display:flex;z-index:13;justify-content:flex-start;align-items:center}.login{padding:20px 10px;position:fixed;width:660px;min-height:266px;top:50%;left:0;right:0;margin:0 auto;background-color:#333;z-index:14;opacity:1;transform:translate3d(0px, -50.09%, 0px);background:#fff}.login h2{text-align:center;margin-bottom:10px}.login p{text-align:center;margin-bottom:10px}.login input[type='text'],.login input[type='password']{width:100%;padding:12px 20px;margin:8px 0;display:inline-block;border:1px solid #ccc}.login button{background-color:#333;color:white;padding:14px 20px;margin:8px 0;border:none;cursor:pointer;width:100%}.login button:hover{opacity:0.8}

.hero{margin-bottom:20px}.hero__container{display:flex}.hero__item{padding:24px 12px 16px;width:100%;text-align:start;overflow:hidden}.hero__item-image{display:block;min-height:300px;padding:0 0 100% 0;position:relative;margin-bottom:10px}.hero__item-img{position:absolute;top:0;left:0;height:100%;width:100%;object-fit:cover}.hero__item-title{font-size:28px;margin-left:10%;margin-bottom:2%;font-weight:400}.hero__item-link{text-decoration:underline;margin-left:10%;letter-spacing:1.6px}

.product{height:390px;max-width:350px;position:relative;color:#000;height:100%;text-align:start;flex:1 1 270px;margin-right:1%}.product:nth-child(4n){margin-right:0}.product__link img{width:100%;height:100%;object-fit:cover}.product__title{font-weight:400;padding:5px 0}.product__title a::before{content:'';position:absolute;top:0;left:0;bottom:0;right:0}.product__desc{opacity:0.6;padding:5px 0}.product__price{padding:5px 0}

.banner_link{display:block;margin:auto;margin-bottom:20px}.banner_link img{width:100%;height:100%;object-fit:cover}

.popular__items{display:flex;justify-content:center;align-items:center;flex-wrap:wrap;margin-bottom:80px}.popular__title{text-align:center;font-size:28px;margin-bottom:40px}

.footer{border-top:1px solid rgba(0,0,0,0.2);background-color:rgba(0,0,0,0.02)}.footer__content{padding-top:100px}.footer__top{display:flex;justify-content:space-between;flex-wrap:wrap}.footer__item:not(:last-child){margin-right:2%;margin-bottom:10px}.footer__link{font-size:13px}.footer__title{font-weight:500;font-size:16px;margin-bottom:20px}.footer__form{width:350px}.subscribe__form{position:relative;margin-bottom:10px}.subscribe__form-title{font-weight:400;font-size:13px;margin-bottom:10px}.subscribe__form-btn{position:absolute;right:0;bottom:0;z-index:2;font-weight:500;font-size:13px;outline:none;border:none;background:transparent}.subscribe__form-field{display:block;width:100%}.subscribe__form-field input{width:100%;border:none;height:40px;font-size:13px;padding-right:80px;font-weight:500;border-bottom:1px solid #000}.subscribe__form-field input::-webkit-input-placeholder{opacity:0.5;color:#454545}.subscribe__form-field input:-ms-input-placeholder{opacity:0.5;color:#454545}.subscribe__form-field input::-ms-input-placeholder{opacity:0.5;color:#454545}.subscribe__form-field input::placeholder{opacity:0.5;color:#454545}

